<template>
    <div id="login">
        <div class="container-login">
            <h3 class="titulo">Ingresá con tu cuenta o <router-link to="/registro">registrate</router-link></h3>
            <button class="btn-google"><img src="../assets/icons/google.svg" alt="">Ingresar con google</button>
            <div class="separador"><p>o</p></div>
            <form action="" class="login-form" @submit="logear()">
                <div class="input-container">
                    <img src="../assets/icons/user.svg" alt="">
                    <input type="text" v-model="email" placeholder="Email" required>
                </div>
                <div class="input-container">
                    <img src="../assets/icons/key.svg" alt="">
                    <input type="password" v-model="contraseña" placeholder="Contraseña" required>
                </div>
                <button type="submit">Iniciar sesión</button>
                <a href="">¿Olvidaste la clave?</a>
            </form>
        </div>
    </div>
</template>

<script>
import {mapMutations} from 'vuex';
export default {
    name: 'Login',
    data() {
        return {
            email: '',
            contraseña: ''
        }
    },
    methods: {
        ...mapMutations(['ingresar']),
        logear: function () {
            event.preventDefault();
            let user = {
                email: this.email,
                contraseña: this.contraseña
            }
            this.ingresar(user);
            this.$router.push({ path: '/' });
        }
    }
}
</script>

<style lang="scss">
    #login {
        //90px tamaño del navegador, 210px tamaño del footer
        min-height: calc(100vh - 90px - 210px);
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        .container-login {
            background: white;
            box-shadow: 1px 1px 6px 0 #ccc;
            border-radius: 6px;
            padding: 40px 50px;
            margin: 10px 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .titulo {
                font-size: 22px;
                margin-bottom: 20px;
                a {
                    color: #de7f71;
                }
            }
            .btn-google {
                padding: 8px;
                outline: none;
                border-radius: 6px;
                background-color: #4285F4;
                color: white;
                transition: all 0.4s ease-in-out;
                border: none;
                width: 100%;
                img {
                    max-width: 21px;
                    max-height: 21px;
                    margin-right: 10px;
                    margin-bottom: 3px;
                }
            }
            .separador {
                position: relative;
                width: 100%;
                margin: 40px 0;
                display: flex;
                justify-content: center;
                p {
                    margin:0;
                    padding: 0 15px;
                    background: white;
                    z-index: 9999;
                    color: #aaa;
                }
            }
            .separador::after {
                content: '';
                height: 1px;
                width: 100%;
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                background: #aaa;
            }
            .login-form {
                text-align: center;
                width: 100%;
                .input-container{
                    position: relative;
                    margin-bottom: 30px;
                    img {
                        width: 20px;
                        height: 20px;
                        position: absolute;
                        top: 50%;
                        transform: translateY(-50%);
                        left: 10px;
                        opacity: 0.7;
                    }
                    input{
                        width: 100%;
                        padding: 8px 5px 8px 40px;
                        border: 1px solid #ccc;
                        border-radius: 6px;
                        outline: none;
                        transition: all 0.4s ease-in-out;
                    }
                    input:focus {
                        border: 1px solid #de7f71;
                    }
                }
                button{
                    display: block;
                    margin: 0 auto 30px auto;
                    padding: 8px;
                    width: 100%;
                    outline: none;
                    border-radius: 6px;
                    background-color: #de7f71;
                    color: white;
                    transition: all 0.4s ease-in-out;
                    border: none;
                }
                button:hover {
                    background-color: #ee8878;
                    color: white;
                }
                a {
                    color: #333;
                    transition: all 0.4s;
                }
                a:hover {
                    color: #de7f71;
                }
            }
        }
    }
    @media (max-width: 700px) {
        #login {
            .container-login {
                margin: 20px;
            }
        }
    }
    @media (max-width: 550px) {
        #login {
            .container-login {
                .titulo {
                    text-align: center;
                }
            }
        }
    }
</style>